@import "./components/variables.scss";

* {
  margin: 0;
  // color: $ltColor;
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
  font-weight: 300;
}

h1 {
  font-size: calc(80px + (115 - 80) * ((100vw - 540px) / (1500 - 540)));
  font-family: "Roboto", serif;
  text-transform: uppercase;
}

h2 {
  font-family: sans-serif;
  font-size: 38px;
  letter-spacing: 0.75px;
  text-transform: uppercase;
  &.section-title {
    margin-bottom: 40px;
    text-align: center;
    text-decoration: underline;
    text-decoration-color: lightgrey;
  }
}
h3 {
  font-size: 20px;
  text-transform: uppercase;
  font-family: sans-serif;
}

h4 {
  text-transform: uppercase;
  font-size: 15px;
  display: inline-block;
  color: $dkColor;
}

p {
  font-family: "Roboto";
  line-height: 1.5;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  // text-transform: uppercase;
  font-size: 15px;
}

a {
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.5s ease-in-out;
  &:link,
  &:visited {
  }
  &:hover,
  &:active {
  }
}

span {
  transition: all 0.5s ease-in-out;
}

img {
  display: block;
  max-width: 100%;
  // margin: 0 auto;
}

section {
  background-color: white;
}

.btn {
  text-transform: uppercase;
  padding: 8px 24px 8px 24px;
  border: none;
  transition: all 0.5s ease-in-out;
}

.btn--transparent {
  background-color: transparent;
}

.btn--lt {
  color: $ltColor;
  border: 1.5px solid $ltColor;
  &:hover {
    color: $dkColor;
    background-color: $ltColor;
    border: 1.5px solid transparent;
  }
}

.btn--dk {
  color: $dkColor;
  border: 1.5px solid $dkColor;
  &:hover {
    color: $ltColor;
    background-color: $dkColor;
    border: 1.5px solid transparent;
  }
}
